import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import StepContent from "@mui/material/StepContent";

const dispatchedEvents = [];

const SrsMuiStepContent = function (props, ref) {
  return <StepContent {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiStepContent component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "alternativeLabel",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "last",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepContent),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
